@import "normalize";
@import "fonts";

$black: #3b3944;

* {
  margin: 0px;
  padding: 0px;
}

.container {
    max-width: 1170px;
    margin: auto;
}

body {
    font-family: 'Roboto', sans-serif;
}

button {
    cursor: pointer;
    outline: none;
    a:hover,
    a:active {
      color: #f2f1f3;
    }
  }

.row, 
.column {
    box-sizing: border-box;
}

.row:before,
.row:after {
    content: " ";
    display: table;
}

.row:after {
    clear: both;
}

.column {
    position: relative;
    float: left;
    display: block;
}

.column + .column {
    margin-left: 1.6%;
}

.column-1 {
    width: 6.86666666667%;
}

.column-2 {
    width: 15.3333333333%;
}

.column-3 {
    width: 23.8%;
}

.column-4 {
    width: 32.2666666667%;
}

.column-5 {
    width: 40.7333333333%;
}

.column-6 {
    width: 49.2%;
}

.column-7 {
    width: 57.6666666667%;
}

.column-8 {
    width: 66.1333333333%;
}

.column-9 {
    width: 74.6%;
}

.column-10 {
    width: 83.0666666667%;
}

.column-11 {
    width: 91.5333333333%;
}

.column-12 {
    width: 100%;
    margin-left: 0;
}

.clearfix::after { 
    content: '';
    clear: both; 
    display: block;
}

a {
  text-decoration: none;
}

.button {
    background-color: #36bde3;
    margin-top: 50px;
    margin-bottom: 35px;
    padding: 10px 50px;
    border: none;
    border-bottom: #2d96cc 4px solid;
    border-radius: 5px;
    .buy {
        display: block;
        font-weight: 900;
        font-size: 27px;
    }
    span {
        font-weight: 500;
        font-size: 21px;
        color: white !important;
    }
}

.sale-form, .order-form {
    background-color: white;
    width: 270px;
    float: right;
    margin-top: 60px;
    text-align: center;
    h2 {
        font-size: 28px;
        font-weight: 700;
        color: #5774b8;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-right: 0;
        span {
            padding-left: 10%;
            padding-right: 10%;
        }
    }
    .old-price, .new-price {
        display: inline-block;
        position: relative;
        width: 49%;
    }
    .old-price {
        &:before,
        &:after {
        content: "";
        position: absolute;
        left: 20px;
        top: 75%;
        width: 50%;
        height: 1px;
        background-color: #f87567;
        }
        &:before {
            transform: rotate(15deg);
        }
        &:after {
        transform: rotate(-15deg)
        }
        span, p {
            color: #737e96;
        }
        span {
            font-size: 13px;
        }
        p {
            font-size: 28px;
            padding-top: 20px;
            span {
                font-size: 16px;
            }
        }
    }
    .new-price {
        border-left: 1px solid #d9ecf2;
        span {
            font-size: 14px;
        }
        p {
            font-size: 44px;
            font-weight: 700;
            color: #36bde3;
            padding-top: 5px;
            span {
                font-size: 16px;
                font-weight: 500;
            }
        }
    }
}
header {
    background-color: #e5f2fa;
    background-image: url(../imgs/bg1.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    .header-block {
        background-image: url(../imgs/bg2.png);
        background-position: 470px 20px;
        background-repeat: no-repeat;
        position: relative;
        .logo {
            float: left;
        }
        nav {
            .menu-list {
                text-align: right;
                padding-top: 22px;
                li {
                    list-style-type: none;
                    display: inline-block;
                    padding-left: 37px;
                    a {
                        color: #3b3944;
                        font-size: 18px;
                    }
                }
            }
        }
        h1 {
            font-size: 64px;
            font-weight: 700;
            color: $black;
            margin: 0;
            padding-top: 65px;
        }
        h2 {
            font-size: 34px;
            font-weight: 500;
            color: #4b69b0;
            padding-top: 15px;
            padding-bottom: 45px;
            padding-right: 15%;
        }
        .list-block {
            position: relative;
            .list {
                li {
                    list-style-type: none;
                    color: #2d96cc;
                    padding-bottom: 22px;
                    span {
                        color: $black;
                        padding-left: 20px;
                        font-weight: 500;
                    }
                }
            }
            .image {
                img {
                    position: absolute;
                    top: 320px;
                    left: 450px;
                    width: 75%;
                }
            }
        }
        .sale-form {
            h2 {
                padding-right: 0;
                padding-bottom: 20px;
                font-size: 28px;
                span {
                    padding: 0 10%;
                }
            }
            h3 {
                padding-top: 30px;
                padding-bottom: 20px;
                color: #5774b8;
                font-size: 18px;
            }
            .countdown {
                display: inline-block;
                .countdown-number {
                    display: inline-block;
                    border-right: 1px solid #e6f2f6;
                    padding-right: 15px;
                    padding-left: 15px;
                    p {
                        width: 44px;
                        border: 1px solid #e6f2f6;
                        border-radius: 180px;
                        background: #fcfcfc;
                        .countdown-time {
                            display: inline-block;
                            padding-top: 12%;
                            padding-bottom: 12%;
                            color: #1e1e1e;
                            font-size: 28px;
                            font-weight: 700;
                        }
                    }
                    .countdown-text {
                        display: inline-block;
                        padding-top: 5px;
                        font-size: 18px;
                        color: #291d61;
                    }
                &:last-child {
                    border-right: none;
                }
                }
            }
            .deadline-message {
                display: none;
            }  
            .visible{
                display: block;
            }
            .hidden{
                display: none;
            }
            button {
                margin-top: 25px;
                margin-bottom: 40px;
            }
        }
        
    }
    
}

.popup-overlay {
    display: none;
    position: fixed;
    z-index: 900;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(250, 250, 250, 0.5);
}

.popup {
    display: none;
    position: fixed;
    left: 50%;
    top: 10%;
    margin-left: -400px;
    z-index: 901;
    max-height: 90%;
    overflow: auto;
    .popup-content {
      position: relative;
        .popup-close {
        position: absolute;
        top: 0px;
        right: 10px;
        text-align: center;
        border: none;
        color: #36bde3;
        background-color: transparent;
        font-size: 40px;
        outline: none;
        z-index: 901;
        }
        .form-block {
            width: 800px;
            height: 428px;
            background-color: #e5f2fa;
            background-image: url(../imgs/bg2.png), url(../imgs/bg1.png);
            background-position: 45% -50%, center bottom;
            background-repeat: no-repeat;
            background-size: 40%, auto;
            .logo-block {
                width: 540px;
                min-height: 430px;
                position: relative;
                p {
                    font-size: 20px;
                    font-weight: 700;
                    text-transform: uppercase;
                    color: #4b69b0;
                    padding-left: 60px;
                    position: absolute;
                    top: 100px;
                }
                .image {
                    img {
                        position: absolute;
                        width: 80%;
                        top: 230px;
                        left: -45px;
                    }
                }
            }
            .order-form {
                height: 432px;
                margin-top: 0;
                position: absolute;
                top: -2px;
                right: 0px;
                .wrapper-name, .wrapper-tel {
                    position: relative;
                    padding-bottom: 15px;
                    font-size: 18px;
                    color: #b5c1de;
                    .icon {
                        position: absolute;
                        left: 35px;
                    }
                    input {
                        width: 180px;
                        height: 50px;
                        padding-left: 45px;
                        border: 1px solid #b5c1de;
                    }
                }
                .wrapper-name {
                    padding-top: 15px;
                    .icon {
                        top: 32px;
                    }
                }
                .wrapper-tel {
                    .icon {
                        top: 18px;
                    }
                }
                button {
                    margin: 0;
                }
            }
        }
    }
}

.symptoms-block {
    .symptoms {
        background-color: #fffffd;
        background-image: url(../imgs/man1.png), url(../imgs/bg3.png);
        background-position: 95% 60%, -102% 48%;
        background-repeat: no-repeat;
        background-size: 40%, auto;
        .icon {
            width: 76px;
            height: 76px;
            background: url(../imgs/sprite.png) no-repeat;
            float: left;
            margin-right: 48px;
        }
        h2 {
            padding: 120px 10% 80px 10%;
            font-weight: 700;
            font-size: 49px;
            color: #2e2c35;
            text-align: center;
            span {
                color: #f56856;
            }
        }
        .symptoms-list {
            padding-bottom: 40px;
            li {
                list-style-type: none;
                padding-bottom: 50px;
                padding-right: 10%;
                .icon1 {
                    background-position: 0 -2px;
                }
                .icon2 {
                    background-position: 0 -88px;
                }
                .icon3 {
                    background-position: 0 -175px;
                }
                h3 {
                    font-weight: 700;
                    font-size: 32px;
                    color: #2e2c35;
                    overflow: hidden;
                }
                p {
                    font-size: 20px;
                    color: #7e7c89;
                    overflow: hidden;
                }
            }
        }
        .note {
            position: relative;
            .border-icon {
                margin-left: 2px;
                margin-bottom: 28px;
                width: 70px;
                height: 3px;
                background-color: #b9cfdd;
            }
            .icon4 {
                background-position: 0 -260px;
            }
            .border-p {
                width: 133px;
                height: 3px;
                background-color: #e5f2fa;
                position: absolute;
                left: 122px;
                top: 0;
            }
            p {
                color: #3b3944;
                font-size: 26px;
                font-weight: 500;
                padding-bottom: 260px;
                padding-right: 22%;
                overflow: hidden;
            }
        }
    }
}


.products-block {
    background-image: url(../imgs/bg1.png);
    background-position: center bottom;
    .products {
        position: relative;
        .cream {
            h2 {
                font-weight: 700;
                font-size: 60px;
                color: #3b3944;
                padding-top: 40px;
                padding-bottom: 35px;
                span {
                    color: #5774b8;
                }
            }
            & > p {
                color: #3b3944;
                font-size: 28px;
                padding-right: 10%;
            }
        }
        .image {
            img {
                position: absolute;
                top: 100px;
                left: 240px;
            }
        } 
    }
}

.storage-block {
    background-color: #fffffd;
    .storage {
        padding-top: 175px;
        h2 {
            font-weight: 700;
            font-size: 46px;
            color: #2e2c35;
            padding-right: 9%;
            padding-bottom: 50px;
            span {
                color: #5774b8;
            }
        }
        .items {
            .item {
                display: block;
                float: left;
                min-height: 386px;
                h3 {
                    font-size: 24px;
                    font-weight: 700;
                    color: #2e2c35;
                    padding-bottom: 15px;
                }
                img {
                    border-top: 3px solid #e0e9ee;
                    width: 235px;
                    height: 171px;
                }
                p {
                    color: #cbcacd;
                    font-size: 18px;
                    padding-top: 30px;
                }
            }
            .item3, .item4 {
                padding-top: 45px;
                padding-bottom: 25px;
            }
            .item1, .item3 {
                width: 273px;
                p {
                    padding-right: 38px;
                }
            }
            .item2, .item4 {
                width: 270px;
                padding-left: 30px;
            }
            .item1 {
                p {
                    padding-right: 38px;
                }
            }
            .item2 {
                p {
                    padding-right: 56px;
                }
            }
            .item3 {
                p {
                    padding-right: 45px;
                }
            }
        }
        
    }
}
    

@import "media";