@media only screen and (max-width: 1200px) {
  header {
    .header-block {
      background-position: 300px 20px;
      nav {
        .menu-list {
          padding-right: 20px;
        }
      }
      .order-form,
      .sale-form {
        margin-right: 20px;
      }
    }
  }
  .products-block {
    .products {
      .image {
        img {
          left: 40px;
        }
      }
    }
  }
  .storage-block {
    .storage {
      padding-top: 100px;
      .items {
        .item {
          min-height: auto;
          padding-bottom: 20px;
          p {
            padding-top: 0;
          }
        }
        .item2,
        .item4 {
          padding-left: 0px;
        }
        .item3,
        .item4 {
          padding-top: 0px;
        }
        .item1,
        .item2,
        .item3,
        .item4 {
          width: 100%;
        }
      }
    }
  }
}

@media only screen and (max-width: 992px) {
  .popup {
    margin-left: -135px;
    .popup-content {
      .form-block {
        width: 270px;
        background-image: none;
        .logo-block {
          display: none;
          .image {
            display: none;
          }
        }
      }
    }
  }
  header {
    .header-block {
      background-position: 55% 60px;
      h1 {
        font-size: 50px;
        padding-top: 30px;
        padding-right: 10%;
      }
      .list-block {
        .image {
          img {
            left: 430px;
          }
        }
      }
      .sale-form {
        margin-top: 0;
      }
    }
  }
  .symptoms-block {
    .symptoms {
      background-position: 85% 250px, -40% 10%;
      h2 {
        padding-bottom: 60px;
        font-size: 40px;
      }
      .symptoms-list {
        padding-bottom: 0;
        li {
          h3 {
            font-size: 24px;
          }
          p {
            font-size: 16px;
          }
        }
      }
      .note {
        p {
          font-size: 18px;
          padding-bottom: 150px;
        }
      }
    }
  }
  .products-block {
    .products {
      .image {
        img {
          left: 0px;
        }
      }
    }
  }
  .storage-block {
    .storage {
      .images {
        img {
          width: 100%;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .row {
    padding-left: 10px;
    padding-right: 10px;
  }
  .column-1,
  .column-2,
  .column-3,
  .column-4,
  .column-5,
  .column-6,
  .column-7,
  .column-8,
  .column-9,
  .column-10,
  .column-11,
  .column-12 {
    float: none;
    width: auto;
  }

  .column + .column {
    margin-left: 0;
  }

  header {
    background-image: none;
    .header-block {
      background-position: 0% 500px;
      .logo {
        width: 35%;
        img {
          width: 90%;
        }
      }
      nav {
        .menu-list {
          li {
            padding-left: 5px;
          }
        }
      }
      .list-block {
        .image {
          display: none;
        }
      }
      .sale-form {
        margin-top: 150px;
        margin-bottom: 20px;
      }
    }
  }
  .symptoms-block {
    .symptoms {
      background-position: 85% 250px, -20% -20%;
      h2 {
        padding-top: 30px;
      }
      .symptoms-list {
        width: 70%;
      }
    }
  }
  .products-block {
    .products {
      .cream {
        h2 {
          font-size: 50px;
        }
      }
      .image {
        width: 50%;
        img {
          left: 250px;
          top: -120px;
          width: 250px;
        }
      }
    }
  }
  .storage-block {
    .storage {
      h2 {
        font-size: 40px;
      }
      .images {
        img {
          display: none;
        }
      }
      .items {
        .item {
          width: 48%;
        }
      }
    }
  }
}

@media only screen and (max-width: 576px) {
  header {
    .header-block {
      .logo {
        width: 50%;
      }
      h1 {
        font-size: 40px;
        padding-right: 0;
      }
      h2 {
        font-size: 30px;
        padding-right: 0;
      }
      nav {
        .menu-list {
          li {
            display: block;
            padding-bottom: 10px;
            a {
                border-bottom: 1px solid gray;
            }
          }
        }
      }
      .sale-form {
        margin-top: 250px;
      }
    }
  }
  .symptoms-block {
    .symptoms {
      background-image: none;
      .symptoms-list {
        width: 100%;
      }
      .note {
        p {
          padding-bottom: 50px;
          padding-right: 10%;
        }
      }
    }
  }
  .products-block {
    .products {
      .cream {
        h2 {
          font-size: 40px;
        }
        & > p {
          font-size: 24px;
        }
      }
      .image {
        img {
          left: 150px;
          top: -50px;
          width: 200px;
        }
      }
    }
  }
  .storage-block {
    .storage {
      padding-top: 120px;
      h2 {
        font-size: 30px;
      }
      .items {
        .item {
          width: 100%;
        }
        .item1,
        .item2,
        .item3,
        .item4 {
            padding-right: 10px;
        }
      }
    }
  }
}
